import React from 'react';
import * as T from './types';
import * as S from './styles';
import { colors } from '../../shared';

type COLORS_OBJECT = {
  key: string;
  variants: {
    variant: string;
    hex: string;
  };
};

let colorsObject: COLORS_OBJECT[];

const keys = Object.keys(colors);
keys.map(key => {
  const variants = Object.keys(colors[key]).map(variant => {
    const hex = colors[key][variant];
    return { variant, hex };
  });
  colorsObject.push({ key, variants });
  console.log(colorsObject);
});

export const Colors: React.FC<T.ColorsProps> = () => {
  return (
    <S.Wrapper>
      {colorsObject.length &&
        colorsObject.map((color, index) => (
          <li>
            <h4>{color.key}</h4>
            <S.List>
              {color.variants &&
                color.variants.map((item, i) => (
                  <S.Content key={i} background={item.hex}>
                    {item.variant}
                  </S.Content>
                ))}
            </S.List>
          </li>
        ))}
    </S.Wrapper>
  );
};
